.card-service {

    > a {
        &:before {
            content: '';
            display: block;
            position: absolute;
            top: 50%;
            left: 0;
            width: 100%;
            height: 100%;
            background: url(../../images/contour.png) center no-repeat;
            background-size: cover;
            transform: translateY(-50%);
        }
    }

    &__tag {
        z-index: 5;
        width: 100%;

        @screen md {
            width: 35px;
            height: 100%;
        }
    }
}